import React, { Component } from "react";
import "./portfolio.css";
import Card from "./Cards/Card";
import CardList from "./Cards/CardList";
import WeatherAppPng from "../imgs/WeatherApp.PNG";
import BlogPng from "../imgs/Blog.PNG";
import YoutubePng from "../imgs/HeaderYoutube.png";
import RecordEditingPng from "../imgs/RecordEditing.PNG";
import LambdaPng from "../imgs/LambdaActions.PNG";
import JSFadePng from "../imgs/JSFade.png";
export class Portfolio extends Component {
  render() {
    return (
      <section
        id="portfolio"
        style={{ backgroundImage: `url(${this.props.bgImage})` }}
      >
      
        <div>
          <h1 className="list-title">Projects</h1>
          <CardList>
            <Card
              title="React Weather App"
              link="https://weather.nubgrammer.com/"
              image={WeatherAppPng}
              footer="My first React app.  For api key security, it uses Firebase to send an api call to Apixu.com for weather data."
            />
            <Card
              title="My Blog"
              link="https://www.nubgrammer.com"
              image={BlogPng}
              footer="This is my blog.  I like to write about things I learn and struggle through so that others can learn from my mistakes."
            />
            <Card
              title="My YouTube Channel"
              link="https://www.youtube.com/channel/UCJJbETbtXuB73zD8FBXKt9w"
              image={YoutubePng}
              footer="This is a YouTube companion channel to my blog."
            />
          </CardList>
          <h1 className="list-title">Posts</h1>
          <CardList>
            <Card
              title=".Net In-place Editing"
              link="https://nubgrammer.com/2019/03/19/ajax-based-in-place-record-editing-in-net/"
              image={RecordEditingPng}
              footer="This post details the mechanics behind in-place, ajax-based record editing using javascript.  I provide a dotNetFiddle demo showing the code from back-end to front-end."
            />
            <Card
              title=".Net Core Lambda Controller Actions"
              link="https://nubgrammer.com/2018/12/09/asp-net-core-lambda-controller-actions/"
              image={LambdaPng}
              footer="In this post, I talk about using Lambda syntax to dramatically shorten simple controllers and speed up development time."
            />
            <Card
              title="Vanilla JS Fade Animation"
              link="https://nubgrammer.com/2018/11/03/anatomy-of-a-javascript-fade/"
              image={JSFadePng}
              footer="Libraries like jQuery and Bootstrap add additional complexity and bloat to applications.  This post details how to accomplish a very common animation effect without the need to import extra, unnecessary code."
            />
          </CardList>
          
        </div>
      </section>
    );
  }
}

export default Portfolio;
