import React, { Component } from "react";
import "./contact.css";
export class Contact extends Component {
  render() {
    return (
      <section
        id="contact"
        style={{ backgroundImage: `url(${this.props.bgImage})` }}
      >
      
        <div className="wrapper">
        <h1>Contact Me</h1>
        <hr/>
        <p>Please feel free to reach out to me for any reason.  I also offer freelance development and Wordpress <a href="https://ageeas.com/" target="_blank">services</a>.</p>
        <p>Complete the form and I will return your email as soon as possible.</p>
        <form action="https://formspree.io/vtsells@gmail.com" method="POST">
          {/* <label for="name">Your Name:</label> */}
          <input type="text" name="name" id="name" placeholder="Your Name" />

          {/* <label for="reply-to">Your Email:</label> */}
          <input type="email" name="_replyto" id="reply-to" placeholder="Your Email"/>

          {/* <label for="body">Your Message:</label> */}
          <textarea name="body" rows="8" id="body" placeholder="Your Message"/>

          <input type="submit" value="Send" />
          <input type="text" name="_gotcha" style={{display:"none"}} />
        </form>
        </div>
      </section>
    );
  }
}

export default Contact;
