import React, { Component } from "react";
import "./about.css";
import me from "../imgs/me.jpg";
export class About extends Component {
  render() {
    return (
      <section
        id="about"
        style={{ backgroundImage: `url(${this.props.bgImage})` }}
      >
        <div className="wrapper">
          
            <img className="med round" src={me} />
            <p>
              Sr. Network Engineer turned developer with a passion for learning
              code. I have led many hardware and software implementations and
              written several applications in various languages. These
              applications are heavily used for daily operational tasks and IT
              compliance at my current employer.
              <hr/>
              I also offer freelance development services.  Visit <a href="https://www.ageeas.com/" target="_blank">ageeas.com</a> for more information.
            </p>
 
            <div className="separator"></div>
            <div className="social-links">
              <a href="https://github.com/vtsells" alt="My Github" target="_blank" title="Github"><i className="fab fa-github fa-4x"></i></a>
              <a href="https://stackoverflow.com/users/8469201/tyler-sells" alt="My Twitter @nubgrammer" target="_blank" title="Stack Overflow"><i className="fab fa-stack-overflow fa-4x"></i></a>
              <a href="https://twitter.com/nubgrammer" alt="My Stackoverflow" target="_blank" title="Twitter"><i className="fab fa-twitter fa-4x"></i></a>
            </div>
        </div>
      </section>
    );
  }
}

export default About;
