import React, { Component } from "react";
import SideNav from "./SideNav/SideNav";
import NavLink from "./SideNav/NavLink";
export class Nav extends Component {

  render() {
    return (
      <SideNav>
        <ul>
          <NavLink className="active" href="#" icon="fas fa-briefcase fa-3x" linkToId="portfolio" containerId="content">
            Portfolio
          </NavLink>
          <NavLink href="#" icon="far fa-address-card fa-3x" linkToId="contact" containerId="content">
            Contact
          </NavLink>
          <NavLink href="#" icon="fas fa-user-circle fa-3x" linkToId="about" containerId="content">
            About
          </NavLink>
        </ul>
      </SideNav>
    );
  }
}

export default Nav;
