import React, { Component } from 'react'
import "./header.css";
export class Header extends Component {
  render() {
    return (
        <header className="sticky-top left">
            <div className="social-links small">
              <a href="https://github.com/vtsells" alt="My Github" target="_blank" title="Github"><i className="fab fa-github"></i></a>
              <a href="https://stackoverflow.com/users/8469201/tyler-sells" alt="My Twitter @nubgrammer" target="_blank" title="Stack Overflow"><i className="fab fa-stack-overflow"></i></a>
              <a href="https://twitter.com/nubgrammer" alt="My Stackoverflow" target="_blank" title="Twitter"><i className="fab fa-twitter"></i></a>
            </div>
            {this.props.children}
        </header>
        
    )
  }
}

export default Header
